import React,{useEffect} from "react";
import "./home.css";
import "../App.css";

function Home() {
  useEffect(() => {
    consoleText(['Front-End-Developer', "Web-Designer", 'React Developer'], 'text',['#ED5E93','#00C9C8']);
  }, [])
  
const consoleText = (words, id, colors) => {
  if (colors === undefined) colors = ['#fff'];
  var visible = true;
  var con = document.getElementById('console');
  var letterCount = 1;
  var x = 1;
  var waiting = false;
  var target = document.getElementById(id)
  target.setAttribute('style', 'color:' + colors[0])
  window.setInterval(function() {

    if (letterCount === 0 && waiting === false) {
      waiting = true;
      target.innerHTML = words[0].substring(0, letterCount)
      window.setTimeout(function() {
        var usedColor = colors.shift();
        colors.push(usedColor);
        var usedWord = words.shift();
        words.push(usedWord);
        x = 1;
        target.setAttribute('style', 'color:' + colors[0])
        letterCount += x;
        waiting = false;
      }, 1000)
    } else if (letterCount === words[0].length + 1 && waiting === false) {
      waiting = true;
      window.setTimeout(function() {
        x = -1;
        letterCount += x;
        waiting = false;
      }, 1000)
    } else if (waiting === false) {
      target.innerHTML = words[0].substring(0, letterCount)
      letterCount += x;
    }
  }, 120)
  window.setInterval(function() {
    if (visible === true) {
      con.className = 'console-underscore hidden'
      visible = false;
    } else {
      con.className = 'console-underscore'
      visible = true;
    }
  }, 400)
}
  return (
    <div className="home" id="home">
        <div className="home__row">
          <div className="home__img">
            <img className="me" src={require("./tahidur.png")} alt="" />
          

          </div>
          <div className="home__text">
            <h1>
              I am
              <div className="name">
                <span className="flicker">T</span>ahidur
                <span className="fast-flicker">R</span>ahman
              </div>,
            </h1>
            <h2>
              a
              <div className="console-container">
                <span id="text"></span>
                <div className="console-underscore" id="console">
                  &#95;
                </div>
              </div>
            </h2>
          </div>
        </div>
    </div>
  );
}

export default Home;
